.navbar {
    background:#ffffff;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }

  .navbar-logo {
    color: #676565;
    justify-self: start;
    margin-bottom: vh;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }

  .social-media{
    color:#676565;
    display:inline-flex;
    padding-left: 8%;
    width:fit-content;
    padding-bottom:5px;
  }

  .fa-lg {
    margin-left: 40%;
  }

  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
  }

  ul {
      padding-left: 0;
  }

  .nav-item {
    height: 80px;
    border-bottom-style: solid;
    border-bottom-color:white;
    border-bottom-width: 1px;
  }

  .nav-item:last-child {
    border: none;
  }

  .nav-links {
    color: #676565;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }

  .nav-links:hover {
    color: #FF00B8;
  }

  .menu-icon {
    display: none;
  }

  .fa-bars {
    color: #676565;
  }

  @media screen and (max-width: 960px) {
      .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: -500%;
        opacity: 1;
        transition: all 0.5s ease 0s;
      }

      .navbar-logo {
          margin-left: 5vw;
      }

      .social-media {
          margin-left: -50vw;
      }

      .nav-menu.active {
        background: #18212e;
        top: 80px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
      }

      .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        color: #fff;
      }

      .nav-links:hover {
        color: #FF00B8;
      }

      .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
      }

      .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
      }

      .fa-times {
        color: #676565;
        font-size: 2rem;
      }

      .fa-bars::before, .fa-times::before  {
        color: #676565;
      }


      .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
      }

      .nav-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
      }
  }


  @media screen and (max-width:520px) {
    .social-media {
        display: none;
    }
  } 